
import { MenuUnfoldOutlined, MenuFoldOutlined } from "@ant-design/icons-vue";
import { defineComponent, onMounted, reactive, ref, toRefs, watch } from "vue";
import { useRouter, useRoute } from "vue-router";
import { SearchOutlined } from '@ant-design/icons-vue';
import { getSideList } from '@/api/index'
import bus from "@/assets/util/bus"
export default defineComponent({
  name: "indexCom",
  components: {
    MenuUnfoldOutlined,
    MenuFoldOutlined,
    SearchOutlined
  },
  setup() {
    const state = reactive({
      sideMenus: [],
      btnDisable: true
    });
    let currentName: string = ""
    onMounted(() => {
      initGetSideList()
    })
    const initGetSideList = () => {
      getSideList().then(res => {
        state.sideMenus = res.data
      })
    }
    watch(useRoute(), (val) => {
      currentName = val.path.replace("/", "")
      state.btnDisable = ["home", "china"].includes(currentName)
    }, {
      immediate: true
    })
    const router = useRouter();
    const goPage = (path: string): void => {
      router.push({ path });
    };
    const searchData = () => {
      bus.emit('searchData')
    }
    return {
      ...toRefs(state),
      selectedKeys: ref<string[]>([currentName]),
      openKeys: ref<string[]>(["sub1"]),
      collapsed: ref<boolean>(false),
      goPage,
      searchData
    };
  },
});
